<template>
  <div id="fourupdata">
    <Music />
    <div class="home_container">
      <div class="iconback" @click="back">
        <img src="./../../assets/back.png" alt="" />
      </div>
      <div class="pm" @click="gotopm">排行榜</div>
      <div class="home_title">
        <p>雀巢咖啡，提醒每一天</p>
        <h1>同学，请放下手机运动吧！</h1>
      </div>
      <div class="home_info">
        <div class="home_info_cont">
          <p>我的步数：</p>
          <p>{{ myData.my_step }}</p>
        </div>
        <div class="home_info_cont">
          <p>已连续打卡：</p>
          <p>{{ myData.days }}天</p>
        </div>
      </div>
      <div class="title">示例:</div>
      <!-- <img style="width: 30px; height: 30px" :src="avatarUrl" />
      <van-uploader
        :before-read="beforeRead"
        :after-read="afterRead"
        accept="image/png, image/jpeg"
