      <image-cropper
        :visible="cropperVisible"
        :url="imgUrl"
        :file-info="fileInfo"
        @cancel="cropperVisible = false"
        @success="cropperSuccess"
      /> -->

      <div class="uploader">
        <!-- <van-uploader :after-read="afterRead" /> -->
        <img v-if="scanData" :src="scanData" alt="" />
        <img v-else src="../../assets/sl.jpg" alt="" />
      </div>
      <div class="tix">
        <p>步骤：打开微信运动 - 步数排行榜 - 个人排行</p>
        <p style="font-size: 0.5067rem; margin-top: 0.1333rem">
          截图保存上传图片
        </p>
      </div>
      <div class="tix">
        <p style="font-size: 0.2667rem; line-height: 0.48rem">
          上传成功即可累计步数参加每周排行
        </p>
      </div>
      <div class="back_btn">
        <div class="back">
          <!-- <ImgCutter @cutDown="goto"  :boxWidth='200' :box-height="300"  :tool="false">上传图片</ImgCutter> -->
          <van-uploader :after-read="goto">
            <!-- <input type="file" id="uploadFile" name="uploadFile" ref="uploadFile" @change="goto($event)" /> -->
            <!-- <imgCut
              ref="Uppicinput"
              @callback="goto"
              :width="200"
              :height="200"
            > -->
            <div>上传图片</div>
            <!-- </imgCut> -->
          </van-uploader>
          <Vue-imageCutting
            v-if="picloalog"
            :picturePath="fileContent"
            :crossDomain="crossDomainData"
            @confirm="confirmMethod"
            @cancel="cancelMethod"
          />
        </div>
        <div class="back">
          <div @click="submit">提交</div>
        </div>
      </div>

      <van-popup v-model="dialogValue">
        <div class="dialog_tis" style="margin-top: 4.2667rem">
          恭喜您上传成功
        </div>
        <div class="dialog_tis">今日步数已累计添加到每周排名</div>
        <div class="dialog_tis">请在周四查看排行榜信息</div>
        <div class="dialog_back">
          <div @click="dialogValue = false">确定</div>
        </div>
      </van-popup>
      <van-loading v-if="loadingData" type="spinner" size="24px"
        >加载中...</van-loading
      >
    </div>
  </div>
</template>
  
  <script>
import {
  GetFourData,
  GetGameupdata,
  getAliOSSConfig,
  GetFourUpdata,
  getJsConfig,
  GetTimeData
} from "@/api/api";
import ImageCropper from "./../pageimg/ImageCropper.vue";
import axios from "axios";
import ImgCutter from "vue-img-cutter";
import { imgCut } from "vue-imgcut";
import Music from "@/components/Music/index.vue";
// Vue.use(VueCropper);
export default {
  data() {
    return {
      picturePathData: "xxx.jpg",
      crossDomainData: false,
      gameId: "",
      gameValue: "",
      dialogValue: false,
      loadingData: false,
      // 个人信息数据
      myData: {
        // 步数
        my_step: "",
        // 连续打卡天数
        days: "",
      },
      // 步数
      stepNum: undefined,
      // 缓存图片信息
      fileContent: "",
      file: null,
      // 识别路径
      scanData: "",
      scanDatas: "",
      //裁剪弹框
      picloalog: false,
      // 显示排行榜
      timedata:''
    };
  },
  components: {
    ImgCutter,
    imgCut,
    Music,
    ImageCropper
  },
  mounted() {
    this.getInfo();
    this.init();
    this.getTime()
  },
  methods: {
    //         @rotate="rotateMethod"
    //         @restore="restoreMethod"
    //         @loading="loadingMethod"
    //         @touching="touchingMethod"
    // touchingMethod(event) {
    //   // console.log(event);
    // },
    // restoreMethod(event) {
    //   console.log(event);
    // },
    // rotateMethod(event) {
    //   console.log(event);
    // },
    // loadingMethod(event) {
    //   console.log(event);
    // },
    // 获取今日周几
    getTime(){
      GetTimeData().then(res=>{
        console.log(res);
        if(res.data.status == 200){
          // this.timedata = res.data.data.week
        }
      })
    },
    init() {
      getJsConfig({
				url: ''
			})
      .then((res) => {
        console.log(res);
				if (res.status === 200) {
					let config = res.data || {}
					jWeixin.config({
						debug: false,
						appId: config.appId,
						timestamp: config.timestamp,
						nonceStr: config.nonceStr,
						signature: config.signature,
						jsApiList: []
					})
					jWeixin.ready(() => {
            jWeixin.hideMenuItems({
              menuList: [
                'menuItem:copyUrl',
                'menuItem:share:appMessage',
                'menuItem:share:timeline',
                'menuItem:share:qq',
                'menuItem:share:weiboApp',
                'menuItem:share:facebook',
                'menuItem:share:QZone'
              ]
            })

            jWeixin.hideOptionMenu()
					})
				} else {
					this.handleError(res)
				}
      })
    },
    cancelMethod(event) {
      // console.log(event);
      this.picloalog = false;
    },
    confirmMethod(event) {
      // 此时初始化步数信息
      this.stepNum = undefined
      // console.log(event);
      // console.log(this.scanData);
      this.picloalog = false;
      this.scanData = event.base64;
      this.getStep();
      // this.dialogValue = true;
    },
    cutDown(event) {
      console.log(event);
    },
    // onOversize() {
    //   Toast("图片大小不能超过10M");
    // },
    // beforeRead(file) {
    //   console.log(file);
    //   if (file.type !== "image/png" && file.type !== "image/jpeg") {
    //     Toast("请上传图片");
    //     return false;
    //   }
    //   return true;
    // },
    // cropperSuccess(file) {
    //   console.log(file);
    //   //裁剪上传完成
    //   this.avatarUrl = "https://" + file.location;
    //   this.cropperVisible = false;
    //   //请求保存头像操作
    //   axios("/upload/xxxx", "post", {})
    //     .then((response) => {
    //       Toast("上传头像成功");
    //     })
    //     .catch((error) => {
    //       Toast("上传头像失败，请重试");
    //     });
    // },
    // afterRead(file) {
    //   //图片加载完成 进行裁剪
    //   file = file.file;
    //   const fileReader = new FileReader();
    //   fileReader.readAsDataURL(file);
    //   fileReader.onload = () => {
    //     //显示裁剪框
    //     this.imgUrl = fileReader.result;
    //     //将文件信息传过去
    //     this.fileInfo = {
    //       name: file.name,
    //       suffix: file.name.substring(file.name.lastIndexOf(".") + 1),
    //     };
    //     console.log(file);
    //     this.cropperVisible = true;
    //   };
    // },
    getInfo() {
      GetFourData().then((res) => {
        console.log(res);
        if (res.data.status == 200) {
          this.myData.days = res.data.data.days;
          this.myData.my_step = res.data.data.my_step;
        }
      });
    },
    // 返回
    back() {
      this.$router.push("/pagefour");
    },

    //上传事件
    async goto(file) {
      console.log(file);
      this.picloalog = true;
      this.fileContent = file.content;
      this.file = file.file;
      // this.dialogValue = true;
      // let dress = file.split(";");
      // console.log(dress);
      // let picd = dress[0].split(":");
      // console.log(picd);
      // file = file.file;
      // const fileReader = new FileReader();
      // fileReader.readAsDataURL(file);
      // fileReader.onload = () => {
      //   //显示裁剪框
      //   this.imgUrl = fileReader.result;
      //   //将文件信息传过去
      //   this.fileInfo = {
      //     name: file.name,
      //     suffix: file.name.substring(file.name.lastIndexOf(".") + 1)
      //   };
        // console.log(file);
        // this.cropperVisible = true;
      // };
    },
    async getStep() {
      this.loadingData = true;
      let key = "wUbxngjGDBUEsS-kulRcXh2Jp-9dXkVM";
      let secret = "gxcRJD0wqwVElf2OemeFMLWqLIo9ywNR";
      let image_base64 = this.scanData;
      let formData = new FormData();
      formData.append("api_key", key);
      formData.append("api_secret", secret);
      formData.append("image_base64", image_base64);
      let scan = await axios({
        url: "https://api-cn.faceplusplus.com/imagepp/v2/generalocr",
        method: "post",
        data: formData,
      });
      this.loadingData = false;
      if (scan.status == 200) {
      //   if (scan.data.text_info[3] == "步数") {
      //     this.myData.my_step = scan.data.text_info[4];
      //   }
        let textInfo = scan.data.text_info
        // 获取日期之前的今日运动索引
        let ydIndex = textInfo.findIndex((v) => {
          return v.line_content === '今日运动'
        })
        if (ydIndex < 0) {
          // 无今日运动字样，提示，中止
          this.Toast('请截图包含今日运动部分')
          return
        }
        let timeInfo = textInfo[ydIndex + 1] || {}
        // 获取日期字符串
        let dateStr = timeInfo.line_content
        // 无日期信息提示
        if (!dateStr || !/^\d+月\d+日/.test(dateStr)) {
          this.Toast('请截图包含日期部分')
          return
        }
        // 解析 月 日 数字
        let matchs = dateStr.match(/\d+/g)
        let month = matchs[0]
        let day = matchs[1]
        let today = new Date()
        // 判断是否是今天的运动截图
        if (!(Number(month) === today.getMonth() + 1 && Number(day) === today.getDate())) {
          this.Toast('截图的运动日期不是今天，请上传今天的运动截图')
          return
        }
        // 获取步数索引
        let stepIndex = textInfo.findIndex((v) => {
          return v.line_content === '步数'
        })
        if (stepIndex < 0) {
          // 无步数字样，提示，中止
          this.Toast('请截图包含步数部分')
          return
        }
        let stepInfo = textInfo[stepIndex + 1] || {}
        // 获取到步数
        let stepNum = stepInfo.line_content
        // 验证步数是否正确
        if (!stepNum || isNaN(stepNum)) {
          this.Toast('请截图包含步数部分')
          return
        }
        // 步数小于xxx提示
        if (stepNum < 100) {
          this.Toast('同学，今天的运动步数不达标哦，需要达到一万步才可以打卡成功')
          return
        }
        // 获取到运动步数
        // console.log(stepNum)
        this.stepNum = stepNum;
      } else {
        this.Toast('获取步数发生错误，请重新上传')
      }
    },
    // 跳转到排名 页面
    gotopm() {
      this.$router.push("/fourpm");
    },
    // 提交图片信息和步数
    async submit() {
      // if (this.myData.my_step == "") {
      //   return this.Toast("请上传图片");
      // }
      // if (this.scanData == "") {
      //   return this.Toast("请上传图片");
      // }
      if (!this.stepNum) {
        return this.Toast("未获取到步数信息，请上传图片");
      }

      this.loadingData = true;

      let res = await getAliOSSConfig();
      let OSSConfig = res.data.data;
      console.log(res);
      const form = new FormData();
      const filename = new Date().getTime() +".jpg";
      console.log(filename);
      form.append("name", `${filename}`);
      form.append("key", OSSConfig.dir + filename);
      form.append("policy", OSSConfig.policy);
      form.append("OSSAccessKeyId", OSSConfig.accessid);
      // form.append("success_action_status", 200);
      form.append("callback", OSSConfig.callback);
      form.append("signature", OSSConfig.signature);
      form.append("file", this.file);
      // console.log(this.file);
      GetGameupdata(OSSConfig.host, form).then((res) => {
        console.log(res);
        this.loadingData = false;
        if (res.data.status == 200) {
          // this.scanData = image_base64;
          this.scanDatas = res.data.data.filename;
          // this.picloalog = true;
          this.submitInfo();
        } else {
          this.Toast("上传图片错误，请重试");
        }
      });
    },
    async submitInfo() {
      this.loadingData = true;
      var date = new Date();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      month = month > 9 ? month : "0" + month;
      day = day < 10 ? "0" + day : day;
      var today = month + "月" + day + "日";
      console.log(today);
      console.log(this.scanData);
      GetFourUpdata({
        // 时间
        date_str: today,
        // 步数
        step_num: this.stepNum,
        // 图片地址
        img_uri: this.scanDatas,
      }).then((res) => {
        console.log(res);
        this.loadingData = false;
        if (res.data.status == 200) {
          this.dialogValue = true;
          this.getInfo();
        } else {
          this.Toast(res.data.msg);
          this.getInfo();
        }
      });
    }
  },
};
</script>
  
